import React,{Component} from 'react';
import { Card, CardImg, CardImgOverlay, CardText, CardBody,
    CardTitle, Breadcrumb, BreadcrumbItem  } from 'reactstrap';
    import { Link } from 'react-router-dom';

    // import CommentForm from './CommentForm';


    import {Button, Modal, ModalHeader , ModalBody,Label,Row,Col,Input} from 'reactstrap';
    import { Control, LocalForm, Errors } from 'react-redux-form';

    const required = (val) => val && val.length;

    const maxLength = (len) => (val) => !(val) || (val.length <= len);
    const minLength = (len) => (val) => val && (val.length >= len);

    class CommentForm extends Component
    {
      constructor(props)
      {
        super(props);

        this.state = {
          ModalState : false
        };
        this.toggleModal = this.toggleModal.bind(this)
      }

      toggleModal()
      {
        this.setState(
          {
            ModalState : !this.state.ModalState
          }
        );
        console.log(this.state.ModalState)
      }
      render()
      {
          return(
          <div className = "container">
            <Button onClick = {this.toggleModal} outline color="secondary"><i className = "fa fa-pencil"></i> Sumbit Comment</Button>
            <Modal isOpen = {this.state.ModalState} toggle = {this.toggleModal} className = "modal-dialog">
              <ModalHeader>Sumbit Comment</ModalHeader>
              <ModalBody>
                <LocalForm>
                  <Row className = "form-group">
                    <Col md = {10}>
                      <Label htmlFor="rating">Rating</Label>
                          <Control.select model=".rating" id="rating" name="rating" className="form-control">
                            <option>Rating</option>
                            <option>5</option>
                            <option>4</option>
                            <option>3</option>
                            <option>2</option>
                            <option>1</option>
                          </Control.select>
                       </Col>
                  </Row>
                  <Row className = "form-group">
                    <Label for="firstname" md={2}>Your Name</Label>
                    <Col md = {10}>
                      <Control.text model=".firstname" id="firstname" name="firstname"
                          placeholder="First Name"
                          className="form-control"
                          validators={{
                              required, minLength: minLength(3), maxLength: maxLength(15)
                          }}
                           />
                           <Errors
                               className="text-danger"
                               model=".firstname"
                               show="touched"
                               messages={{
                                   required: 'Required',
                                   minLength: 'Must be greater than 2 characters',
                                   maxLength: 'Must be 15 characters or less'
                               }}
                            />
                       </Col>
                  </Row>
                  <Row className="form-group">
                      <Label htmlFor="message" md={2}>Comment</Label>
                      <Col md={10}>
                          <Control.textarea model=".message" id="message" name="message"
                              rows="6"
                              className="form-control" />
                      </Col>
                  </Row>
                  <Row className="form-group">
                      <Col md={10}>
                          <Button color = "primary">Sumbit</Button>
                      </Col>
                  </Row>

                </LocalForm>
              </ModalBody>
            </Modal>
          </div>

        );
      }
    }


  function RenderDish({dish})
  {
    return(
        <Card>
            <CardImg top src={dish.image} alt={dish.name} />
            <CardBody>
              <CardTitle>{dish.name}</CardTitle>
              <CardText>{dish.description}</CardText>
            </CardBody>
        </Card>


    );
  }

  function RenderComments({commentsArr, addComment, dishId}) {

    if(commentsArr != null)
    {
        return(commentsArr.map( (usercomment) =>
        {
              return(<div>
                <li className = "list-unstyled">{usercomment.comment}</li>
              <br></br>
              <li className = "list-unstyled"> -- {usercomment.author},{new Intl.DateTimeFormat('en-US', { year: 'numeric', month: 'short', day: '2-digit'}).format(new Date(Date.parse(usercomment.date)))}</li>
              <br></br>
              </div>
            );
        }));
    }
    else{
      return(<div></div>);
    }


  }

  const Dishdetail = (props) =>
  {
    if (props.dish != null){
      return (
                <div className="container">
                <div className="row">
                    <Breadcrumb>

                        <BreadcrumbItem><Link to="/menu">Menu</Link></BreadcrumbItem>
                        <BreadcrumbItem active>{props.dish.name}</BreadcrumbItem>
                    </Breadcrumb>
                    <div className="col-12">
                        <h3>{props.dish.name}</h3>
                        <hr />
                    </div>
                </div>
                <div className="row">
                    <div className="col-12 col-md-5 m-1">
                        <RenderDish dish={props.dish} />
                    </div>
                    <div className="col-12 col-md-5 m-1">
                      <RenderComments commentsArr={props.comments}
                        addComment={props.addComment}
                        dishId={props.dish.id}
                      />
                    </div>
                  <CommentForm dishId={props.dishId} addComment={props.addComment} />
                </div>
                </div>
            );
      }


    else
        return(
            <div></div>
        );
    }


export default Dishdetail;
